.intro{
    display: flex;
    height: 77vh;
    margin-top: 6rem;
}
.i-left{
    display: flex;
    position: relative;
    flex:1;
    flex-direction: column;
    gap: 2rem;

}

.i-name{
    display: flex;
    flex-direction: column;
}

.i-name>:nth-child(1)
{
    color: var(--black);
    font-weight: bold;
    font-size: 3rem;
}

.i-name>:nth-child(2)
{
    color: var(--orange);
    font-weight: bold;
    font-size: 3rem;
}
.i-name>:nth-child(3)
{
    font-weight: 100;
    font-size: 14px;
    color: var(--grey);
    margin-top: 10px;
}
.i-button{
    width: 6rem;
    height: 2rem;
}
.i-icons{
    margin-top: 3rem;
    display: flex;
    gap: 0rem;
}
 .i-icons>*{
    transform: scale(0.5);
 }

 .i-icons>*:hover{
    cursor: pointer;
 }





.i-right{
    flex:0.5;
    position: relative;
}
.i-right>*{
    position: absolute;
    z-index: 1;
}

.i-right>:nth-child(1)
{
    transform: scale(0.69);
    left: -16%;
    top: -8%;
}
.i-right>:nth-child(2)
{
    top: -5.0rem;
    transform: scale(0.67);
    left: -5rem;
}
.i-right>:nth-child(3)
{
    transform: scale(1.4);
    left: 26%;
}
.i-right>:nth-child(4)
{
    transform: scale(0.6);
    left: -24%;
    top: -19%;
    border-radius: 50%;
    padding: 0;
}
.blur{
    position: absolute;
    width: 22rem;
    height: 14rem;
    border-radius: 50%;
    background: #edd0ff;
    z-index: -9;
    top: -18%;
    left: 56%;
    filter: blur(72px);
}